import React, { Component } from 'react'
import { connect } from 'react-redux'
import { createProject } from '../../redux/actions/projectActions'
import { Redirect, NavLink } from 'react-router-dom'

class CreateProject extends Component {
state = {
    title: '',
    name: '',
    content: '',
    imageData: [],
    imageUrl: '',
    videoData: [],
    videoUrl: ''
}

handleChange = (e) => {
  this.setState({
    [e.target.id]: e.target.value
  })
}

handleSubmit = (e) => {
  e.preventDefault();
  this.props.createProject(this.state)
  this.props.history.push('/');
}

handleImageUpload = (e) => {
  const values = e.target.files;
  [...values].forEach(file => {
  this.setState({ imageData: file, imageUrl: URL.createObjectURL(file)})
  })
}


handleVideoUpload = (e) => {
  const values = e.target.files;
  [...values].forEach(file => {
  this.setState({ videoData: file, videoUrl: URL.createObjectURL(file)})
  })
}

render() {
  const { auth } = this.props
  if (!auth.uid) return <Redirect to='/signin' />

    return (
        <div className="container">
          <form onSubmit={this.handleSubmit} className="white">
            <h5 className="grey-text text-darken-3"> Report Public or Private Persons' Spending Ananymously</h5>
            <p className="grey-text text-darken-3"> Select from below or type if not found</p>
            <div className="input-field">
            <label htmlFor="title"> Name </label>
            <select required className='form-group form-control' onChange={this.handleChange} >
                <option>Select Name</option>
                <option>President Muhammad Buhari</option>
                <option>Mike Adenuga</option>
                <option>Gov. Sanwolu</option>
                <option>Sen. Ahmed Lawan</option>
                <option>Alh. Dangote</option>
                <option>Atiku Abubakar</option>
                <option>Peter Obi</option>
                <option>Rotimi Ameichi</option>
            </select>
            </div>
            <div className="input-field">
              <label htmlFor="name"> Name </label>
              <input type="text" id="title" onChange={this.handleChange} />
            </div>
            <div className="input-field">
              <label htmlFor="content"> Content </label>
              <textarea id="content" className="materialize-textarea" onChange={this.handleChange}> </textarea>
            </div>
            <div className="file-field input-field">
            <div class="btn blue">
                <span>Upload Image</span>
                <input onChange={this.handleImageUpload} accept="image/*" type="file" multiple/>
            </div>
            <div class="file-path-wrapper">
              <input class="file-path validate" type="text" placeholder="Upload one or more images" />
            </div>
            </div>
            <div className="file-field input-field">
            <div class="btn blue">
                <span>Upload Video</span>
                <input onChange={this.handleVideoUpload} accept="video/*" type="file" multiple />
            </div>
            <div class="file-path-wrapper">
              <input class="file-path validate" type="text" placeholder="Upload one or more videos" />
            </div>
            </div>
            <div className="input-field">
              <button className="btn pink lighten-1 z-index-0">Create</button>
            </div>
          </form>
          <div>
          <NavLink to='/'>
            <button className="btn green lighten-1 z-index-0">View other ananymous reports</button>
          </NavLink>
          </div>
        </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    auth: state.firebase.auth
  }
}

function mapDispatchToProps(dispatch) {
  return {
    createProject: (project) => dispatch(createProject(project))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateProject)
